'use client'

import { useState } from 'react'
import Link from 'next/link'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Progress } from '@/components/ui/progress'
import { RadioGroup, RadioGroupItem } from '@/components/ui/radio-group'
import { Label } from '@/components/ui/label'

interface Question {
  id: number
  text: string
  options: string[]
}

const questions: Question[] = [
  {
    id: 1,
    text: "What is your skin type?",
    options: ["Dry", "Oily", "Combination", "Sensitive", "Normal"]
  },
  {
    id: 2,
    text: "What is your primary skin concern?",
    options: ["Acne/Breakouts", "Aging/Fine Lines", "Dryness", "Redness/Irritation", "Uneven Skin Tone"]
  },
  {
    id: 3,
    text: "How often do you currently use skincare products?",
    options: ["Daily (morning & evening)", "Daily (once a day)", "Few times a week", "Occasionally", "Rarely"]
  },
  {
    id: 4,
    text: "What's your age range?",
    options: ["Under 18", "18-25", "26-35", "36-45", "46+"]
  },
  {
    id: 5,
    text: "Do you have any specific ingredient preferences?",
    options: ["Natural/Organic only", "Fragrance-free", "Hypoallergenic", "No specific preference", "Vegan products"]
  },
  {
    id: 6,
    text: "What's your main goal for your skincare routine?",
    options: ["Hydration", "Anti-aging", "Acne control", "Gentle care", "Overall skin health"]
  }
]

interface Recommendation {
  title: string
  description: string
  products: string[]
  routine: string[]
}

export default function ConsultationPage() {
  const [currentStep, setCurrentStep] = useState(0)
  const [answers, setAnswers] = useState<{ [key: number]: string }>({})
  const [showResults, setShowResults] = useState(false)
  const [error, setError] = useState('')

  const handleAnswer = (questionId: number, answer: string) => {
    setAnswers({ ...answers, [questionId]: answer })
    setError('')
  }

  const nextStep = () => {
    const currentQuestion = questions[currentStep]
    if (!answers[currentQuestion.id]) {
      setError('Please select an answer before proceeding.')
      return
    }

    if (currentStep === questions.length - 1) {
      setShowResults(true)
      return
    }

    setCurrentStep(currentStep + 1)
  }

  const previousStep = () => {
    if (currentStep > 0) {
      setCurrentStep(currentStep - 1)
      setError('')
    }
  }

  const restartQuiz = () => {
    setAnswers({})
    setCurrentStep(0)
    setShowResults(false)
    setError('')
  }

  const generateRecommendation = (): Recommendation => {
    const skinType = answers[1]
    const concern = answers[2]
    const goal = answers[6]

    // Simple recommendation logic based on answers
    if (skinType === 'Dry' || concern === 'Dryness' || goal === 'Hydration') {
      return {
        title: 'Hydrating & Nourishing Routine',
        description: 'Your skin needs deep hydration and nourishment. We recommend our moisturizing collection with natural ingredients.',
        products: [
          'Coconut Dreams Moisturizer Cream',
          'Coconut Shower Gel',
          'Dew Chocolate Lip Balm',
          'Fresh Mint Foot Cream'
        ],
        routine: [
          'Morning: Gentle cleanse with Coconut Shower Gel',
          'Apply Coconut Dreams Moisturizer Cream',
          'Use Dew Chocolate Lip Balm throughout the day',
          'Evening: Moisturize with foot cream before bed'
        ]
      }
    } else if (skinType === 'Oily' || concern === 'Acne/Breakouts') {
      return {
        title: 'Balancing & Clarifying Routine',
        description: 'Your skin needs gentle cleansing and oil control. Our natural formulas help balance without over-drying.',
        products: [
          'Kiwi Shower Gel',
          'Micellar Cleansing Water',
          'Lip Gloss Set',
          'Deodorant Dream'
        ],
        routine: [
          'Morning: Cleanse with Kiwi Shower Gel',
          'Use Micellar Water for gentle makeup removal',
          'Apply Deodorant Dream for all-day freshness',
          'Evening: Light lip care with our Lip Gloss'
        ]
      }
    } else if (skinType === 'Sensitive' || concern === 'Redness/Irritation') {
      return {
        title: 'Gentle & Soothing Routine',
        description: 'Your sensitive skin needs extra gentle care. Our hypoallergenic products provide comfort and protection.',
        products: [
          'Sunshine Splash Moisturizer Cream',
          'Strawberry Lip Scrub',
          'Fresh Breeze Foot Cream',
          'Sunblock SPF 50++'
        ],
        routine: [
          'Morning: Apply Sunshine Splash Moisturizer',
          'Use Sunblock SPF 50++ for protection',
          'Gentle lip care with Strawberry Lip Scrub (weekly)',
          'Evening: Soothing foot care with Fresh Breeze cream'
        ]
      }
    } else {
      return {
        title: 'Complete Skincare Routine',
        description: 'A well-rounded routine to maintain healthy, glowing skin with our bestselling products.',
        products: [
          'Dew Bundle (Complete Set)',
          'Rosemary Shampoo & Conditioner',
          'LipBalm x3 Set',
          'Cookie LipBalm'
        ],
        routine: [
          'Morning: Complete cleansing routine with Dew Bundle',
          'Hair care with Rosemary Shampoo & Conditioner',
          'Lip care with LipBalm collection',
          'Evening: Moisturize and treat with Cookie LipBalm'
        ]
      }
    }
  }

  if (showResults) {
    const recommendation = generateRecommendation()
    
    return (
      <div className="min-h-screen bg-gradient-to-br from-green-50 to-blue-50">
        <header className="bg-white shadow-sm border-b">
          <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8">
            <div className="flex justify-between items-center py-6">
              <Link href="/" className="text-2xl font-bold text-gray-900">
                Dew SkinCare
              </Link>
              <span className="text-sm text-gray-500">Consultation Results</span>
            </div>
          </div>
        </header>

        <main className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
          <Card className="mb-8">
            <CardHeader className="text-center">
              <CardTitle className="text-3xl text-green-600">{recommendation.title}</CardTitle>
              <CardDescription className="text-lg mt-4">
                {recommendation.description}
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-8">
              <div>
                <h3 className="text-xl font-semibold mb-4">Recommended Products:</h3>
                <div className="grid md:grid-cols-2 gap-4">
                  {recommendation.products.map((product, index) => (
                    <div key={index} className="bg-green-50 p-4 rounded-lg">
                      <p className="font-medium text-green-800">{product}</p>
                    </div>
                  ))}
                </div>
              </div>

              <div>
                <h3 className="text-xl font-semibold mb-4">Your Personalized Routine:</h3>
                <div className="space-y-3">
                  {recommendation.routine.map((step, index) => (
                    <div key={index} className="flex items-start space-x-3">
                      <span className="bg-green-600 text-white rounded-full w-6 h-6 flex items-center justify-center text-sm font-medium">
                        {index + 1}
                      </span>
                      <p className="text-gray-700">{step}</p>
                    </div>
                  ))}
                </div>
              </div>

              <div className="flex flex-col sm:flex-row gap-4 pt-6">
                <Button onClick={restartQuiz} variant="outline" className="flex-1">
                  Take Quiz Again
                </Button>
                <Button asChild className="flex-1 bg-green-600 hover:bg-green-700">
                  <Link href="https://www.dewskincaree.com">
                    Shop These Products
                  </Link>
                </Button>
              </div>
            </CardContent>
          </Card>
        </main>
      </div>
    )
  }

  const currentQuestion = questions[currentStep]
  const progress = ((currentStep + 1) / questions.length) * 100

  return (
    <div className="min-h-screen bg-gradient-to-br from-green-50 to-blue-50">
      <header className="bg-white shadow-sm border-b">
        <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center py-6">
            <Link href="/" className="text-2xl font-bold text-gray-900">
              Dew SkinCare
            </Link>
            <span className="text-sm text-gray-500">Skincare Consultation</span>
          </div>
        </div>
      </header>

      <main className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
        <Card>
          <CardHeader>
            <div className="space-y-4">
              <div className="flex justify-between items-center">
                <CardTitle className="text-2xl">Question {currentStep + 1} of {questions.length}</CardTitle>
                <span className="text-sm text-gray-500">{Math.round(progress)}% Complete</span>
              </div>
              <Progress value={progress} className="w-full" />
            </div>
          </CardHeader>
          <CardContent className="space-y-6">
            <h2 className="text-xl font-medium">{currentQuestion.text}</h2>
            
            <RadioGroup
              value={answers[currentQuestion.id] || ''}
              onValueChange={(value) => handleAnswer(currentQuestion.id, value)}
            >
              {currentQuestion.options.map((option, index) => (
                <div key={index} className="flex items-center space-x-2 p-3 rounded-lg hover:bg-gray-50 border border-transparent hover:border-gray-200">
                  <RadioGroupItem value={option} id={`option-${index}`} />
                  <Label htmlFor={`option-${index}`} className="flex-1 cursor-pointer">
                    {option}
                  </Label>
                </div>
              ))}
            </RadioGroup>

            {error && (
              <div className="text-red-600 text-sm bg-red-50 p-3 rounded-lg">
                {error}
              </div>
            )}

            <div className="flex justify-between pt-6">
              <Button
                onClick={previousStep}
                variant="outline"
                disabled={currentStep === 0}
              >
                Previous
              </Button>
              <Button
                onClick={nextStep}
                className="bg-green-600 hover:bg-green-700"
              >
                {currentStep === questions.length - 1 ? 'Get My Recommendations' : 'Next'}
              </Button>
            </div>
          </CardContent>
        </Card>
      </main>
    </div>
  )
}
